import { useSelector } from 'react-redux';
import AppState from '../../AppState';
import { IdsRange } from '../../types';
import SettingsPage from '../../components/SettingsPage';
import SettingsParam from '../../components/SettingsParam';
import SettingsToggle from '../../components/SettingsToggle';
import { useAppDispatch } from '../../app/store';
import { updateVoice } from './voicesSlice';
import { basename } from '../../utils';

type VoiceSettingsProps = {
  trackId: IdsRange
};

function VoiceSettings({ trackId }: VoiceSettingsProps) {
  const voice = useSelector((s: AppState) => s.voices[trackId]);
  const dispatch = useAppDispatch();
  // TODO handle bad voice id
  return (
    <SettingsPage title={voice.name || `Voice ${voice.id}`}>
      <SettingsParam label="Gain" value={voice.gain} />
      <SettingsParam label="URL" value={basename(voice.url)} />
      <SettingsToggle
        label="Mute"
        active={voice.muted}
        onToggle={() => {
          dispatch(updateVoice({
            id: voice.id,
            muted: !voice.muted
          }));
        }}
      />
      <SettingsToggle
        label="Reverse"
        active={voice.reverse}
        onToggle={() => {
          dispatch(updateVoice({
            id: voice.id,
            reverse: !voice.reverse
          }));
        }}
      />
    </SettingsPage>
  );
}

export default VoiceSettings;
