import React from 'react';

type TabButtonProps = {
  children?: React.ReactNode,
  onClick?: (e?: React.MouseEvent<HTMLButtonElement>) => void,
  selected?: boolean
};

function TabButton({ children, onClick, selected = false }: TabButtonProps) {
  return (
    <button
      disabled={selected}
      onClick={onClick}
      type="button"
      className={`p-1 border-t border-l last:border-r ${selected ? 'bg-slate-500 text-pink-200' : 'bg-slate-300 dark:bg-slate-700 text-purple-800 dark:text-purple-300'} border-slate-500 active:border-yellow-400 first:rounded-tl last:rounded-tr font-digital font-bold text-sm`}
    >
      {children}
    </button>
  );
}

export default TabButton;
