import React from 'react';
import { StepState } from '../types';

type SequencerButtonProps = {
  children?: React.ReactNode,
  step: StepState,
  onClick?: (e?: React.MouseEvent<HTMLButtonElement>) => void,
  currentStep?: boolean,
};

function SequencerButton({
  children, step, onClick, currentStep = false
}: SequencerButtonProps) {
  return (
    <button
      className={`group ${step.active ? 'bg-green-400' : 'bg-slate-400'} border-2 rounded md:h-14 hp-12 md:w-10 w-8 ${currentStep ? 'border-yellow-400' : 'border-gray-500'} active:border-sky-400 col justify-center items-center relative md:m-1 m-0.5`}
      onClick={onClick}
    >
      <span
        className="absolute top-0 left-0 w-full h-full z-10 bg-white/0 group-active:bg-white/30"
      />
      <span className="font-digital text-black pt-3">
        {children}
      </span>
      {}
    </button>
  );
}

export default SequencerButton;
