import { createSlice, PayloadAction } from '@reduxjs/toolkit';
import { PadMode } from '../../types';

export type PadsState = {
  mode: PadMode
};

const initialState: PadsState = {
  mode: 'PAD_MODE_SELECT'
};

export const padsSlice = createSlice({
  name: 'pads',
  initialState,
  reducers: {
    setPadMode: (state, action: PayloadAction<PadMode>) => {
      state.mode = action.payload;
    }
  },
});

export const {
  setPadMode
} = padsSlice.actions;

export default padsSlice.reducer;
