import { useRef } from 'react';
import { Loop } from 'tone';
import { Seconds, Time } from 'tone/build/esm/core/type/Units';

/* eslint-disable @typescript-eslint/no-unused-vars */
export default function useEventLoop (
  callback?: (time?: Seconds) => void,
  interval: Time = '8n',
): Loop {
  //
  const loop = useRef<Loop>(new Loop(callback, interval));

  return loop.current;
}
