import { useSelector } from 'react-redux';
import AppState from '../../AppState';
import SquareButton from '../../../shared/components/SquareButton';
import { useAppDispatch } from '../../app/store';
import { togglePlaying } from './transportSlice';
import { setCurrentStep } from '../sequencer/sequencerSlice';

function TransportCtrls() {
  const playing = useSelector((s: AppState) => s.transport.playing);
  const tracks = useSelector((s: AppState) => s.sequencer.tracks);
  const dispatch = useAppDispatch();
  return (
    <div className="row justify-center items-center m-2 p-2 rounded bg-slate-400/40">
      <SquareButton
        className={`md:text-5xl text-4xl m-0.5 md:m-1 ${playing ? 'bg-green-500' : 'bg-slate-600'} border-slate-400`}
        onClick={() => {
          dispatch(togglePlaying());
        }}
      >
        ►
      </SquareButton>
      <SquareButton
        className={'md:text-5xl text-4xl m-0.5 md:m-1 bg-slate-600 active:bg-yellow-500 border-slate-400'}
        onClick={() => {
          Object.values(tracks).forEach((track) => {
            dispatch(setCurrentStep({
              trackId: track.id,
              currentStep: 1,
            }));
          });
        }}
      >
        ⏎
      </SquareButton>
    </div>
  );
}

export default TransportCtrls;
