import { useSelector } from 'react-redux';
import AppState from '../../AppState';
import { useAppDispatch } from '../../app/store';
import NumberInput from '../../components/NumberInput';
import { setTempo } from './transportSlice';

function TempoCtrl() {
  const { tempo } = useSelector((s: AppState) => s.transport);
  const dispatch = useAppDispatch();
  return (
    <div className="col justify-center items-center rounded bg-slate-400/40">
      <NumberInput
        col
        labelClassName="justify-center items-center"
        label="BPM"
        min={30}
        max={300}
        value={tempo}
        onChange={(e) => {
          dispatch(setTempo(Number(e?.target.value)));
        }}
      />
    </div>
  );
}

export default TempoCtrl;
