import { useDispatch } from 'react-redux';
import { configureStore } from '@reduxjs/toolkit';
import pads from '../features/pads/padsSlice';
import voices from '../features/voices/voicesSlice';
import transport from '../features/transport/transportSlice';
import tracks from '../features/tracks/tracksSlice';
import sequencer from '../features/sequencer/sequencerSlice';
import settings from '../features/settings/settingsSlice';

const store = configureStore({
  reducer: {
    pads,
    voices,
    sequencer,
    transport,
    tracks,
    settings,
  }
});

export type AppDispatch = typeof store.dispatch
export const useAppDispatch = useDispatch.withTypes<AppDispatch>();

export default store;
