import React from 'react';

type PadButtonProps = {
  children?: React.ReactNode,
  style?: React.CSSProperties,
  className?: string,
  onClick: (e?: React.MouseEvent<HTMLButtonElement>) => void
};

function PadButton({
  children,
  style,
  className,
  onClick,
}: PadButtonProps) {
  return (
    <button
      type="button"
      className={`group relative md:w-16 md:h-16 h-14 w-14 border-2 rounded-md ${className}`}
      style={{
        ...style
      }}
      onClick={onClick}
    >
      <span className="w-full h-full absolute top-0 left-0 bg-white/0 group-active:bg-white/40" />
      <span className="w-full h-full col justify-end items-start p-1 font-digital z-20 text-sm md:text-base">
        {children}
      </span>
    </button>
  );
}

export default PadButton;
