import { createSlice, PayloadAction } from '@reduxjs/toolkit';
import { Time } from 'tone/build/esm/core/type/Units';
import {
  SequencerTrackState, SequencerState, StepState, IdsRange 
} from '../../types';
import { idIterator } from '../../utils';

type StepUpdatePayload = {
  trackId: IdsRange,
  stepId: IdsRange,
  data?: {
    active?: boolean,
    duration?: Time,
  }
};

type CurrentStepPayload = {
  trackId: IdsRange,
  currentStep: IdsRange,
};

const initialState = (): SequencerState => {
  const tracks: Record<number, SequencerTrackState> = {};
  idIterator.forEach((trackId) => {
    const steps: Record<number, StepState> = {};
    idIterator.forEach((stepId) => {
      if (stepId <= 16) {
        steps[stepId] = {
          id: stepId,
          active: false,
          duration: '8n',
        };
      }
    });
    tracks[trackId] = {
      id: trackId,
      steps,
      currentStep: 1,
      lastStep: null,
    };
  });
  return {
    tracks
  };
};

export const sequencerSlice = createSlice({
  name: 'sequencer',
  initialState,
  reducers: {
    updateStep: (state, action: PayloadAction<StepUpdatePayload>) => {
      if (state.tracks[action.payload.trackId]?.steps[action.payload.stepId] !== undefined) {
        state.tracks[action.payload.trackId].steps[action.payload.stepId] = {
          ...state.tracks[action.payload.trackId].steps[action.payload.stepId],
          ...action.payload.data
        };
      }
    },
    setCurrentStep: (state, action: PayloadAction<CurrentStepPayload>) => {
      const track = state.tracks[action.payload.trackId];
      if (track !== undefined) {
        // console.log('setting current step');
        state.tracks[action.payload.trackId].lastStep = track.currentStep;
        state.tracks[action.payload.trackId].currentStep = action.payload.currentStep;
      }
    }
  },
});

export const {
  setCurrentStep,
  updateStep
} = sequencerSlice.actions;

export default sequencerSlice.reducer;
