import React from 'react';

type SettingsToggleProps = {
  label?: string,
  active?: boolean,
  onToggle?: () => void,
};

function SettingsToggle({ label, active = false, onToggle }: SettingsToggleProps) {
  return (
    <div className="row justify-between items-center my-2 w-full">
      <span className="mr-2">
        {label}
      </span>
      
      <button
        className={`${active ? 'bg-green-400' : 'bg-gray-500'} border border-slate-600 active:border-slate-400 h-4 w-4`}
        type="button"
        onClick={onToggle}
      >
        {}
      </button>
    </div>
  );
}

export default SettingsToggle;
