import { useSelector } from 'react-redux';
import SettingsPage from '../../components/SettingsPage';
import AppState from '../../AppState';

function SongEditor() {
  const { tracks } = useSelector((s: AppState) => s.sequencer);
  return (
    <SettingsPage>
      {Object.values(tracks).map((track) => (
        <div
          key={`song-editor-seq-track-${track.id}`}
          className="row justify-start items-center py-[0.075rem] odd:bg-slate-500/30 even:bg-blue-500/30"
        >
          {Object.values(track.steps).map((step) => (
            <div
              key={`song-editor-seq-track-${track.id}-step-${step.id}`}
              className={`rounded-full w-2 h-2 border ${track.currentStep === step.id ? 'border-yellow-500' : 'border-slate-500'} ${step.active ? 'bg-green-500' : 'bg-slate-600'} m-[0.075rem]`}
            />
          ))}
        </div>
      ))}
    </SettingsPage>
  );
}

export default SongEditor;
