import React from 'react';

type SquareButtonProps = {
  children?: React.ReactNode,
  onClick?: (e?: React.MouseEvent) => void,
  style?: React.CSSProperties,
  className?: string,
};

function SquareButton({
  children,
  onClick,
  style,
  className,
}: SquareButtonProps) {
  return (
    <button
      style={{
        ...style,
      }}
      className={`group col justify-center items-center relative md:h-14 md:w-14 w-12 h-12 rounded border-2 ${className}`}
      type="button"
      onClick={onClick}
    >
      <span className="group-active:bg-white/30 bg-white/0 absolute w-full h-full z-10 top-0 left-0" />
      <span className="w-full h-full col justify-center items-center z-20">
        {children}
      </span>
    </button>
  );
}

export default SquareButton;
