import { useSelector } from 'react-redux';
import AppState from '../../AppState';
import VoiceSettings from '../voices/VoiceSettings';
import { useState } from 'react';
import TabButton from '../../components/TabButton';
import TrackSettings from '../tracks/TrackSettings';
import SettingsEditor from '../settings/SettingsEditor';
import SongEditor from '../sequencer/SongEditor';
import Credits from '../credits/Credits';

type TabName = 'VOICE_EDITOR' | 'SEQUENCE_EDITOR' | 'SONG_EDITOR' | 'SETTINGS_EDITOR' | 'CREDITS';
type TabButton = {
  name: TabName,
  label: string,
};
const tabButtons: TabButton[] = [
  {
    name: 'VOICE_EDITOR',
    label: 'Voice'
  },
  {
    name: 'SEQUENCE_EDITOR',
    label: 'Seq'
  },
  {
    name: 'SONG_EDITOR',
    label: 'Song'
  },
  {
    name: 'SETTINGS_EDITOR',
    label: 'Settings'
  },
  {
    name: 'CREDITS',
    label: 'Credits'
  },
];

function EditorSection() {
  const { selectedTrack } = useSelector((s: AppState) => s.tracks);
  const [tab, setTab] = useState<TabName>('VOICE_EDITOR');
  return (
    <div className="lg:w-1/4 md:w-1/3 w-5/6 col m-2 h-full">
      <div className="row w-full overflow-x-scroll whitespace-nowrap">
        {tabButtons.map(({ name, label }) => (
          <TabButton
            key={`${name}-tab-button`}
            onClick={() => setTab(name)}
            selected={tab === name}
          >
            {label}
          </TabButton>
        ))}
      </div>
      <div className="col justify-start items-start flex-1 bg-slate-400/40 rounded-b border border-slate-500 p-2">
        {/* TODO tidy up tnis */}
        {tab === 'VOICE_EDITOR' && <VoiceSettings trackId={selectedTrack} />}
        {tab === 'SEQUENCE_EDITOR' && <TrackSettings trackId={selectedTrack} />}
        {tab === 'SONG_EDITOR' && <SongEditor />}
        {tab === 'SETTINGS_EDITOR' && <SettingsEditor />}
        {tab === 'CREDITS' && <Credits />}
      </div>
    </div>
  );
}

export default EditorSection;
