import React from 'react';

type NumberInputProps = {
  max?: number,
  min?: number,
  value: number,
  label?: string,
  onChange: (e?: React.ChangeEvent<HTMLInputElement>) => void,
  id?: string,
  col?: boolean,
  labelClassName?: string,
};

function NumberInput({
  max,
  min,
  value,
  onChange,
  id,
  label,
  labelClassName,
  col = false
}: NumberInputProps) {
  return (
    <label
      className={`${col ? 'col' : 'row'} ${labelClassName}`}
      htmlFor={id}
    >
      {label && (
        <span>
          {label}
        </span>
      )}
      <input
        className="p-1 m-1 bg-black text-pink-400 font-digital border-2 rounded border-slate-500"
        id={id}
        onChange={onChange}
        value={value}
        min={min}
        max={max}
        type="number"
      />
    </label>
  );
}

export default NumberInput;
