import { useSelector } from 'react-redux';
import AppState from '../../AppState';
import SequencerButton from '../../components/SequencerButton';
import { useAppDispatch } from '../../app/store';
import { updateStep } from './sequencerSlice';
import { IdsRange } from '../../types';
import { idIterator } from '../../utils';

type StepButtonsProps = {
  trackId: IdsRange
};

function StepButtons({ trackId }: StepButtonsProps) {
  const track = useSelector((s: AppState) => s.sequencer.tracks[trackId]);
  const dispatch = useAppDispatch();
  if (track === undefined) {
    // ERROR!
    console.error(`undefined track: ${trackId}`);
    return null;
  }
  // console.log(track);
  return (
    <div className="row justify-start items-center w-fit flex-wrap">
      {idIterator.map((id) => track.steps[id] && (
        <SequencerButton
          currentStep={id === track.currentStep}
          step={track.steps[id]}
          key={`sequencer-step-button-${id}`}
          onClick={() => {
            dispatch(updateStep({
              trackId,
              stepId: id,
              data: {
                active: !track.steps[id].active
              }
            }));
          }}
        >
          {id}
        </SequencerButton>
      ))}
    </div>
  );
}

export default StepButtons;
