import { useSelector } from 'react-redux';
import PadButton from '../../../shared/components/PadButton';
import { padButtonRows } from './support';
import AppState from '../../AppState';
import { useAppDispatch } from '../../app/store';
import { setSelectedTrack } from '../tracks/tracksSlice';
import { PadMode, Voice } from '../../types';

type PadsProps = {
  playVoice: (voice: Voice) => void
}

function getPadsClassName(mode: PadMode, selected: boolean): string {
  let className = '';
  if (mode === 'PAD_MODE_SELECT') {
    className += selected ? 'bg-purple-500 border-yellow-400' : 'bg-blue-500 border-slate-400';
  } else if (mode === 'PAD_MODE_TRIGGER') {
    className += 'bg-sky-600 border-slate-400 active:border-blue-400 active:bg-pink-500';
  }
  return className;
}

function Pads({ playVoice }: PadsProps) {
  const voices = useSelector((s: AppState) => s.voices);
  const { selectedTrack } = useSelector((s: AppState) => s.tracks);
  const { mode } = useSelector((s: AppState) => s.pads);
  const dispatch = useAppDispatch();
  return (
    <div className="flex flex-col-reverse justify-start items-start p-1 rounded bg-blue-400/40">
      {padButtonRows.map((buttons, id) => (
        <div
          key={`pad-row-${id}`}
          className="row justify-start items-center"
        >
          {buttons.map((button) => (
            <PadButton
              className={`md:m-1 m-0.5 uppercase ${getPadsClassName(mode, button.id === selectedTrack)}`}
              key={`pad-button-${button.id}`}
              onClick={() => {
                if (mode === 'PAD_MODE_SELECT' && selectedTrack !== button.id) {
                  dispatch(setSelectedTrack(button.id));
                } else if (mode === 'PAD_MODE_TRIGGER' || selectedTrack === button.id) {
                  playVoice(voices[button.id]);
                }
              }}
            >
              {voices[button.id]?.shorthand}
            </PadButton>
          ))}
        </div>
      ))}
    </div>
  );
}

export default Pads;
