/* eslint-disable @typescript-eslint/no-unused-vars */
import { useSelector } from 'react-redux';
import Ctrls1 from './features/ctrls/Ctrls1';
import EditorSection from './features/editor/EditorSection';
import Pads from './features/pads/Pads';
import Sequencer from './features/sequencer/Sequencer';
import TransportCtrls from './features/transport/TransportCtrls';
import { useEventLoop, useVoices } from './hooks';
import AppState from './AppState';
import { useEffect } from 'react';
import { getTransport, now } from 'tone';
import { useAppDispatch } from './app/store';
import { setCurrentStep } from './features/sequencer/sequencerSlice';
import { IdsRange } from './types';
import TempoCtrl from './features/transport/TempoCtrl';
import { Seconds } from 'tone/build/esm/core/type/Units';
// import { Time } from 'tone/build/esm/core/type/Units';

function SampleBox() {
  const { playing, tempo } = useSelector((s: AppState) => s.transport);
  const { tracks } = useSelector((s: AppState) => s.sequencer);
  const { restartOnPlay } = useSelector((s: AppState) => s.settings);
  const dispatch = useAppDispatch();
  const voices = useVoices();
  // usePlayers();
  const loopHandler = (time: Seconds) => {    
    Object.values(tracks).forEach((track) => {
      // console.log(track.steps[track.currentStep]);
      if (track.steps[track.currentStep as IdsRange]?.active
        && voices[track.id].loaded
      ) {
        // console.log(`Track ${track.id} - Step ${track.currentStep}`);
        voices[track.id].start(time);
      }
      // TODO schedule this properly
      dispatch(setCurrentStep({
        trackId: track.id,
        currentStep: (track.currentStep >= 16 ? 1 : track.currentStep + 1) as IdsRange,
      }));
    });
  };
  const loop = useEventLoop(undefined, '8n');
  // console.log(tracks);
  loop.callback = loopHandler;

  useEffect(() => {
    getTransport().bpm.value = tempo;
  }, [tempo]);

  useEffect(() => {
    if (!playing && loop.state === 'started') {
      // console.log('stopping');
      loop.stop();
      getTransport().stop();
    } else if (playing && loop.state === 'stopped') {
      // console.log('starting');
      if (restartOnPlay) {
        Object.values(tracks).forEach((track) => {
          dispatch(setCurrentStep({
            trackId: track.id,
            currentStep: 1,
          }));
        });
      }
      loop.callback = loopHandler;
      loop.start('8n');
      getTransport().start(now());
    }
  }, [playing]);

  // useEffect(() => {
  //   if (playing) {
  //     Object.values(tracks).forEach((track) => {
  //       // console.log(track.steps[track.currentStep]);
  //       if (track.steps[track.currentStep as IdsRange]?.active
  //         && voices[track.id].loaded
  //       ) {
  //         // console.log(`Track ${track.id} - Step ${track.currentStep}`);
  //         voices[track.id].start(now());
  //       }
  //     });
  //   }
  // }, [tracks, playing]);

  return (
    <div className="w-full min-h-full col justify-center items-center">
      <div className="row justify-center items-center p-1 m-1 w-full">
        <TempoCtrl />
        <TransportCtrls />
        <Ctrls1 />
      </div>
      <div className="md:flex-row w-full flex flex-col-reverse justify-center items-center p-1 m-1 md:h-80 h-fit">
        <Pads
          playVoice={(voice) => {
            if (voices[voice.id]?.loaded) voices[voice.id].start();
          }}
        />
        <EditorSection />
      </div>
      <div className="row justify-center items-center w-full lg:w-3/4 md:w-4/5">
        <Sequencer />
      </div>
    </div>
  );
}

export default SampleBox;
