import { useState } from 'react';
import * as Tone from 'tone';
import SampleBox from './apps/sampler/SampleBox';
// import Monobox from './apps/monosynth/Monobox';

function App() {
  const [booted, setBooted] = useState(false);

  return (
    <div className="col justify-start items-center w-full min-h-full dark:bg-slate-950 dark:text-pink-300">
      {!booted ? (
        <button
          className="m-2 p-3 text-2xl font-mono font-bold hover:underline bg-yellow-300 text-black border-2 border-slate-500 hover:border-yellow-500 active:bg-green-300 active:border-green-500 rounded-lg"
          type="button"
          onClick={async () => {
            await Tone.start();
            setBooted(true);
          }}
        >
          Start
        </button>
      ) : (
        // <Monobox />
        <SampleBox />
      )}
    </div>
  );
}

export default App;
