import { useSelector } from 'react-redux';
import SquareButton from '../../../shared/components/SquareButton';
import AppState from '../../AppState';
import { useAppDispatch } from '../../app/store';
import { setPadMode } from '../pads/padsSlice';

function Ctrls1() {
  const { mode: padMode } = useSelector((s: AppState) => s.pads);
  const dispatch = useAppDispatch();
  return (
    <div className="row justify-center items-center p-2 m-2 rounded bg-slate-400/40">
      <SquareButton
        className={`border-slate-400 active:border-purple-400 ${padMode === 'PAD_MODE_SELECT' ? 'bg-blue-500' : 'bg-pink-500'} text-white md:text-sm text-xs`}
        onClick={() => {
          dispatch(setPadMode(
            padMode === 'PAD_MODE_SELECT' ? 'PAD_MODE_TRIGGER' : 'PAD_MODE_SELECT'
          ));
        }}
      >
        <span
          className="p-1 mb-0.5"
        >
          TRK
        </span>
        <span
          className="p-1 mt-0.5"
        >
          TRG
        </span>
      </SquareButton>
      {}
    </div>
  );
}

export default Ctrls1;
