import { createSlice, PayloadAction } from '@reduxjs/toolkit';
import { voices } from './support';
import { IdsRange, Voice } from '../../types';

export type VoicesState = Record<number, Voice>;

type VoiceUpdate = {
  id: IdsRange,
  name?: string,
  shorthand?: string,
  gain?: number,
  url?: string,
  muted?: boolean,
  reverse?: boolean,
};

const initialState: VoicesState = voices;

export const voicesSlice = createSlice({
  name: 'voices',
  initialState,
  reducers: {
    updateVoice: (state, action: PayloadAction<VoiceUpdate>) => {
      if (state[action.payload.id] !== undefined) {
        state[action.payload.id] = {
          ...state[action.payload.id],
          ...action.payload
        };
      }
    },
  },
});

export const { updateVoice } = voicesSlice.actions;

export default voicesSlice.reducer;
